import React from 'react';
import css from '../scss/read.module.scss';
import {graphql} from 'gatsby';
import {Helmet} from 'react-helmet';
import formatDate from '../helpers/formatDate';
import Img from 'gatsby-image';

export default ({ data: { hero, content: { html, frontmatter } } }) => (
	<>
		<Helmet bodyAttributes={{ class: css.body }}>
			<title>{frontmatter.title}</title>
		</Helmet>
		<header>
			<h1>{frontmatter.title}</h1>
			<Img
				fixed={hero.childImageSharp.fixed}
				alt={frontmatter.title}
			/>
		</header>
		<time>Published {formatDate(frontmatter.date)}</time>
		<article dangerouslySetInnerHTML={{__html:html}} />
	</>
);

export const pageQuery = graphql`
	query ($path: String!) {
		hero: file (
			relativePath: {eq: "../imgs/kor-reunion.jpg"}
            sourceInstanceName: { eq: "imgs" }
		) {
			childImageSharp {
				fixed (width: 1800, height: 800) {
					...GatsbyImageSharpFixed_withWebp
				}
			}
		}
		content: markdownRemark (frontmatter: { path: { eq: $path } }) {
			html
			frontmatter {
				title
				date
			}
		}
	}
`;
